import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router, ROUTES } from '@angular/router';
import { AppStateService } from '../app.state.service';
import { BasePageComponent } from '../base-page.component';

declare var ng: any;

@Component({
  selector: 'app-exp',
  templateUrl: './exp.component.html',
  styleUrls: ['./exp.component.scss'],
  preserveWhitespaces: true,
  encapsulation: ViewEncapsulation.Emulated

})
export class ExpComponent extends BasePageComponent implements OnInit {
  ngOnInit() { }
  year: number;
  old: number;
  showChallenge = false;
  constructor(router: Router, private route: ActivatedRoute, appState: AppStateService) {
    super(router, appState);
    this.year = new Date().getFullYear() - new Date(2018, 7).getFullYear();
    this.old = new Date().getFullYear() - new Date(1995, 10, 25).getFullYear();
  }
  goChallenge(link?: string) {
    // this.appState.accessRoute('/post/cashe');
    this.router.navigateByUrl(link);
  }
}
